
const host = "https://furigana-lychee.herokuapp.com/";
// const host = "http://localhost:8080/"

const fetchRequest = (url, params={}, timeout=10000) => {
    let isTimeout = false;
    return new Promise(function(resolve, reject) {
        const TO = setTimeout(function() {
            isTimeout = true;
            reject(new Error('Fetch timeout'));
        }, timeout);

        fetch(url, params)
            .then(res => {
                clearTimeout(TO)
                if(!isTimeout) {
                    resolve(res)
                }
            }).catch(e => {
                if( isTimeout ){
                    return
                }
                reject(e)
            })
    })
}

class API {
    
    static async wakeUpServer() {
        fetchRequest(host, null, 5 * 1000)
            .then(res => {})
            .catch(err => console.error(err))
    }

    static async query(str) {
        const response = await fetch(host, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            },
            body: JSON.stringify({text: str}),
        }).then(res => res.json());
        
        return response;
    }

    static async report(title, content, payload) {
        const response = await fetch(`${host}/report`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            },
            body: JSON.stringify({
                text: `${title}: \`${content}\`\n\`\`\`${payload}\`\`\``
            }),
        }).then(res => res.text());
        return response;
    }
}

export default API